.App {
  text-align: left;
}

.container {
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
}

a {
	color: rgba(17, 21, 39, 1);
}

.shop-list-item {
    font-size: 1.5em;
    font-weight: 500;
	color: rgba(17, 21, 39, 1);
}

.price table {
    border-collapse: collapse;
    border-spacing:0;
    border:1px #e6e6e6 solid;
    width:100%;
}

.price table th,.price table td {
    padding:5px 5px;
    vertical-align:  middle;
}

.price table th {
    border:#e6e6e6 solid;
    border-width:0 0 1px 1px;
    vertical-align:middle;
    text-align:center;
    background:rgba(17, 21, 39,1);
    color: white;
    height: 50px;
}

.price table td {
    border:1px #e6e6e6 solid;
    border-width:0 0 1px 1px;
    text-align:center;
}

.price table tr:nth-of-type(even) {
    background:#f2f2f2;
}

#store_rentalspace .price table th {
    background:rgba(17, 21, 39,1);
}

.btn-pio-contact {
    color: #fff;
    background-color: #8EC6FF;
    border-color: #C7DCEF;
}
.btn-pio-contact:hover {
    color: black;
}

.control-label {
	font-size: 1.1em;
}

@media screen and (max-width:640px) {
    .price table thead {
        display:none;
    }
    .price table tr td:first-child {
        border-top:1px #e6e6e6 solid !important;
    }
    .price table tr:nth-of-type(even) {
        background:white;
    }
    .price table tr td:last-child {
        width:auto;
    }
    .price table td {
        width:100%;
        display:block;
        text-align:left;
    }
    .price table tr td:before {
        font-weight:bold;
    }
    .price table tr td:nth-of-type(1) {
        background:rgba(17, 21, 39,1);
        color:white;
    }
    .price table tr td.price_type:before {
        content:"【タイプ】";
    }
    .price table tr td.price_number:before {
        content:"【畳数】";
    }
    .price table tr td.price_size:before {
        content:"【幅×奥行×高さ(cm)】";
    }
    .price table tr td.price_official:before {
        content:"【正規料金】";
    }	
    .price table tr td.price_campaign:before {
        content:"【キャンペーン料金】";
    }
    .price table tr td.price_initial:before {
        content:"【初期費用】";
    }
    .price table tr td.price_remarks:before {
        content:"【備考】";
    }
    .price table tr td.price_order {
        text-align:center;
    }
    #store_rentalspace .price table tr td:nth-of-type(1) {
        background:rgba(17, 21, 39,1);
    }
    .price table tr td.acc_txt{
        display: none;
    }
    .price_type .btn-link{
        color: black;
    }
}


.info table th {
    border: #e6e6e6 solid;
    border-width: 0 0 1px 1px;
    vertical-align: top;
    text-align: left;
    background: rgba(17, 21, 39,1);
    color: black;
    width: 20%;
}

div#shop_banner {
    padding: 0px 0px 5px 0px;
}

div#shop_banner .title {
    padding: 15px 10px 15px 10px;
    background: white;
    font-size: 2.0em;
    font-weight: bold;
    color: rgba(17, 21, 39,1);
    text-align: center;
}

div#shop_banner .details {
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 1.5em;
    color: rgba(17, 21, 39,1);
    text-align: center;
}

div.terms-and-agreements{
    padding-bottom: 15px;
}

.react-datepicker-popper {
    z-index: 100 !important;
}

.react-datepicker__input-container {
    height: 35px !important;
}

.react-datepicker__input-container input {
    height: 30px !important;
}
